export function mapMenusToRoutes(userMenus) {
    const routes = [];
    // 1. 先加载默认所有的routes
    const allRoutes = [];
    const routeFiles = require.context('../router/main', true, /\.ts/);
    routeFiles.keys().forEach((key) => {
        const route = require('../router/main' + key.split('.')[1]);
        allRoutes.push(route.default);
    });
    // 2. 根据菜单获取需要添加的routes
    const _recurseGetRoute = (menus) => {
        for (const menu of menus) {
            if (menu.type === 2) {
                const route = allRoutes.find((route) => route.path === menu.url);
                if (route) {
                    routes.push(route);
                }
            }
            else {
                _recurseGetRoute(menu.children);
            }
        }
    };
    _recurseGetRoute(userMenus);
    return routes;
}
export const pathMapToMenu = (userMenus, currentPath, breadcrumb) => {
    const matchParentRoute = userMenus.find((item) => currentPath.startsWith(item.url));
    if (matchParentRoute) {
        const matchRoute = matchParentRoute.children.find((item) => item.url === currentPath);
        breadcrumb?.push({ name: matchParentRoute.name });
        breadcrumb?.push({ name: matchRoute.name });
        return matchRoute.id + '';
    }
    return 'a';
};
export const pathToBreadcrumb = (userMenus, currentPath) => {
    const breadcrumb = [];
    pathMapToMenu(userMenus, currentPath, breadcrumb);
    return breadcrumb;
};
