import { accountLoginRequest, getUserinfoById, getUserMenuByRoleId } from '@/service/user';
import { defineStore } from 'pinia';
import router from '@/router';
import storage from '@/utils/storage';
import { mapMenusToRoutes } from '@/utils/map-menus';
export default defineStore('user', {
    state: () => {
        return {
            token: '',
            userInfo: {},
            userMenu: [] // ts默认初始化空数组为never[]，所以要转换成any[]
        };
    },
    actions: {
        setToken(token) {
            this.token = token;
        },
        setUserInfo(userinfo) {
            this.userInfo = userinfo;
        },
        setUserMenu(userMenu) {
            this.userMenu = userMenu;
            const routes = mapMenusToRoutes(userMenu);
            routes.forEach((route) => {
                router.addRoute('main', route);
            });
        },
        async accountLogin(req) {
            // 1. 登录获取token
            const res = await accountLoginRequest(req);
            const { id, token } = res.data;
            storage.set('token', token);
            this.setToken(token);
            // 2. 请求用户数据
            const userinfoRes = await getUserinfoById(id);
            const userinfo = userinfoRes.data;
            storage.set('userInfo', userinfo);
            this.setUserInfo(userinfo);
            // 3. 请求用户菜单
            const roleId = userinfo.role.id;
            const menuRes = await getUserMenuByRoleId(roleId);
            const menu = menuRes.data;
            storage.set('userMenus', menu);
            this.setUserMenu(menu);
            router.push('/main');
        },
        loadLocalLogin() {
            const token = storage.get('token');
            if (token) {
                this.setToken(token);
            }
            const userInfo = storage.get('userInfo');
            if (userInfo) {
                this.setUserInfo(userInfo);
            }
            const userMenus = storage.get('userMenus');
            if (userMenus) {
                this.setUserMenu(userMenus);
            }
        }
    }
});
