import request from '..';
export const accountLoginRequest = (account) => {
    return request.post('/login', {
        data: account
    });
};
export const getUserinfoById = (id) => {
    return request.get(`/users/${id}`, {
        showLoading: false
    });
};
export const getUserMenuByRoleId = (roleId) => {
    return request.get(`/role/${roleId}/menu`, {
        showLoading: false
    });
};
